import style from "./Tookanomix.module.scss";
import number25 from "../../assets/png/tookanomix/25.png";
import number75 from "../../assets/png/tookanomix/75.png";
import liquidity from "../../assets/png/tookanomix/liquidity.png";
import toocanomix from "../../assets/png/tookanomix/toocanomix.png";
import unlocked from "../../assets/png/tookanomix/unlocked.png";
import morio_on_solana from "../../assets/png/tookanomix/morio-on-solana.png";

export const Tookanomix = () => {
    return (
        <div className={style.tookanomix}
             id="toocanomix"
        >
            <div className={style.inner}>
                <div className={style.item}>
                    <img src={toocanomix} alt=""/>
                    <img src={morio_on_solana} alt=""/>
                </div>
                <div className={style.item}>
                    <img src={liquidity} alt=""/>
                    <img src={number75} alt=""/>
                </div>
                <div className={style.item}>
                    <img src={unlocked} alt=""/>
                    <img src={number25} alt=""/>
                </div>
            </div>
        </div>
    )
}